import React, { Component } from 'react'
import styled from 'styled-components'

const TextArea = styled.div`
  width: 100%;
  height: 400px;
  border-radius: 10px;
  background: #eee;
`

class AppTextArea extends Component {
  render() {
    return <TextArea />
  }
}

export default AppTextArea



